export const stringHelper = () => {
    const dotToComma = (number) => {
        return number.toString().replace(".", ",")
    }
    
    const commaToDot = (number) => {
        return number.toString().replace(",", ".")
    }

    return {
        dotToComma: dotToComma,
        commaToDot: commaToDot
    }
}

