<template>
  <div class="header">
    <h3 style="color: white; margin:0 0 25px 0; ">
      Ajouter une transaction
    </h3>

    <div class="p-grid p-fluid">
      <div class="p-col-12 p-md-4">
        <div class="p-inputgroup">
          <div class="p-float-label">
            <InputText id="transactionLabel" v-model="transactionName" />
            <label for="transactionLabel">Nom de la transaction</label>
          </div>
        </div>
      </div>

      <div class="p-col-12 p-md-4">
        <div class="p-inputgroup">
          <InputNumber
            id="transactionAmount"
            v-model="transactionAmount"
            placeholder="Montant en €"
            mode="currency"
            currency="EUR"
            locale="fr-FR"
          />
          <span class="p-inputgroup-addon">
            <i class="material-icons" @click="changeSign('transactionAmount')">
              iso
            </i>
          </span>
          <span class="p-inputgroup-addon">
            <i :class="plusIcon" @click="addTransaction()" />
          </span>
        </div>
      </div>
      <div class="p-col-12 p-md-4">
        <Dropdown
          v-model="selectedCategory"
          :options="categoryList"
          optionLabel="name"
          placeholder="Sélectionner une catégorie"
        >
          <template #value="slotProps">
            <div v-if="slotProps.value" class="dropdownRow">
              <Avatar
                :icon="slotProps.value.icon"
                :style="
                  `background-color:${
                    slotProps.value.color
                  }; color:${fontContrastColor(slotProps.value.color)};`
                "
                class="avatarBody"
              />
              <div>{{ slotProps.value.name }}</div>
            </div>
            <span v-else class="dropdownRow">
              {{ slotProps.placeholder }}
            </span>
          </template>
          <template #option="slotProps">
            <div class="dropdownRow">
              <Avatar
                :icon="slotProps.option.icon"
                :style="
                  `background-color:${
                    slotProps.option.color
                  }; color:${fontContrastColor(slotProps.option.color)};`
                "
                class="avatarBody"
              />
              <div>{{ slotProps.option.name }}</div>
            </div>
          </template>
        </Dropdown>
      </div>
    </div>

    <Message
      v-if="!validate"
      style="position: absolute; bottom: 0; width: 220px; "
      severity="error"
      :life="2000"
      :sticky="false"
      closable="false"
    >
      Merci de remplir les deux champs
    </Message>
  </div>

  <div
    style="width: 300px; margin: auto; height: 59vh"
    :class="{ 'no-overflow': modifiedTransaction.id === '' }"
  >
    <Card
      v-for="transaction in transactionList"
      :key="transaction.id"
      :style="`margin: 0.3rem; background-color: ${transaction.color};`"
      :class="{
        invisible:
          modifiedTransaction.id !== transaction.id &&
          modifiedTransaction.id !== '',
      }"
    >
      <template #content>
        <div
          v-if="modifiedTransaction.id === ''"
          style="display: flex; text-align: center; width:100%;"
        >
          <Avatar
            :icon="transaction.icon"
            :style="
              `background-color:${transaction.color}; color:${fontContrastColor(
                transaction.color
              )}; margin:-30px 0 0 -15px;`
            "
          />
          <div style="flex: 9">
            <div :style="`color:${fontContrastColor(transaction.color)};`">
              {{ transaction.name }} : {{ transaction.amount }} €
            </div>
          </div>
          <div style="flex: 1">
            <i
              :class="trashIcon"
              :style="`color:${fontContrastColor(transaction.color)};`"
              @click="removeTransaction(transaction)"
            />
          </div>
          <div style="flex: 1">
            <i
              :class="pencilIcon"
              :style="`color:${fontContrastColor(transaction.color)};`"
              @click="editTransaction(transaction)"
            />
          </div>
        </div>

        <div
          v-if="modifiedTransaction.id === transaction.id"
          style="display: flex; flex-direction: column; align-items: flex-start; width:100%;"
        >
          <div style="width:100%;">
            <InputText v-model="transaction.name" style="width: 100%" />
          </div>

          <div style="display: flex; flex-direction: row">
            <div class="p-inputgroup">
              <InputNumber
                id="editingTransactionAmount"
                v-model="transaction.amount"
                placeholder="Montant en €"
                mode="currency"
                currency="EUR"
                locale="fr-FR"
                format="false"
              />
              <span class="p-inputgroup-addon">
                <i
                  class="material-icons"
                  @click="changeSign('editingTransactionAmount', transaction)"
                >
                  iso
                </i>
              </span>
              <span class="p-inputgroup-addon">
                <i :class="timesIcon" @click="cancelTransaction(transaction)" />
              </span>
              <span class="p-inputgroup-addon">
                <i
                  :class="checkIcon"
                  @click="validateTransaction(transaction)"
                />
              </span>
            </div>
          </div>

          <div
            style="display: flex; flex-direction: column; align-self: stretch;"
          >
            <Dropdown
              v-model="editedCategory"
              :options="categoryList"
              optionLabel="name"
              placeholder="Sélectionner une catégorie"
            >
              <template #value="slotProps">
                <div v-if="slotProps.value" class="dropdownRow">
                  <Avatar
                    :icon="slotProps.value.icon"
                    :style="
                      `background-color:${
                        slotProps.value.color
                      }; width:36px; height:36px; margin-top: 5px; margin-bottom:5px; color:${fontContrastColor(
                        slotProps.value.color
                      )}`
                    "
                  />
                  <div>{{ slotProps.value.name }}</div>
                </div>
                <span v-else class="dropdownRow">
                  {{ slotProps.placeholder }}
                </span>
              </template>
              <template #option="slotProps">
                <div class="dropdownRow">
                  <Avatar
                    :icon="slotProps.option.icon"
                    :style="
                      `background-color:${
                        slotProps.option.color
                      }; width:36px; height:36px; margin-top: 5px; margin-bottom:5px; margin-right:5px; color:${fontContrastColor(
                        slotProps.option.color
                      )}`
                    "
                  />
                  <div>{{ slotProps.option.name }}</div>
                </div>
              </template>
            </Dropdown>
          </div>
        </div>
      </template>
    </Card>
  </div>
</template>

<script>
import { useTransactions } from "../composables/useTransactions";
import { category } from "../composables/useCategory";
import { defineComponent, onMounted, ref } from "vue";
import { database, TRANSACTION_REF } from "../services/firebaseDatabase";
import { iconList } from "../helpers/iconsHelper";
import { fontColorHelper } from "../helpers/fontColorHelper";

export default defineComponent({
  name: "TransactionInput",
  props: {
    totalCallback: {
      type: Function,
      required: true,
      default: () => {},
    },
  },
  setup() {
    let modifyName = ref("");
    let modifyAmount = ref(0);
    let total = ref(0);
    const {
      cancelTransaction,
      validateTransaction,
      removeTransaction,
      addTransaction,
      transactionName,
      transactionList,
      transactionAmount,
      modifiedTransaction,
      validate,
      timeoutAlert,
      getTransactions,
      editTransaction,
      changeSign,
      selectedCategory,
      editedCategory,
    } = useTransactions();

    const {
      checkIcon,
      timesIcon,
      plusIcon,
      trashIcon,
      pencilIcon,
    } = iconList();

    const { categoryList, getCategoryByName } = category();

    const { fontContrastColor } = fontColorHelper();

    const transactionRef = database.ref(TRANSACTION_REF);
    const eraseZeroOnFocus = (event) => {
      const el = event.target;
      if (el.value === "0") {
        el.value = "";
      }
    };

    onMounted(() => {
      transactionRef.on("value", (snapshot) => {
        console.log(
          "transactions : \n" + JSON.stringify(snapshot.val(), null, 2)
        );
      });
      getTransactions();
    });

    return {
      addTransaction,
      removeTransaction,
      cancelTransaction,
      validateTransaction,
      transactionName,
      transactionList,
      transactionAmount,
      modifyName,
      modifyAmount,
      modifiedTransaction,
      total,
      validate,
      timeoutAlert,
      editTransaction,
      eraseZeroOnFocus,
      changeSign,
      categoryList,
      selectedCategory,
      getCategoryByName,
      editedCategory,
      checkIcon,
      timesIcon,
      plusIcon,
      trashIcon,
      pencilIcon,
      fontContrastColor,
    };
  },
  watch: {
    transactionList: {
      handler(liste) {
        if (liste.length > 0) {
          this.total = liste
            .map((x) => x.amount)
            .reduce((a, b) => parseFloat(a) + parseFloat(b));
        } else {
          this.total = 0;
        }
        this.totalCallback(this.total);
      },
      deep: true,
    },
  },
});
</script>

