import { ref } from "vue";
import { database, CATEGORY_REF } from "../services/firebaseDatabase";

export const category = () => {
  let selectedColor = ref();
  let selectedCategoryName = ref();
  let selectedCategoryIcon = ref();
  let editingCategory = ref();
  let categoryList = ref([]);

  const saveCategory = () => {
    if (selectedCategoryName.value) {
      const keygen = database.ref(CATEGORY_REF).push()
      database
        .ref(`${CATEGORY_REF}/${keygen.key}`)
        .set({
          name: selectedCategoryName.value,
          color: `#${selectedColor.value}`,
          icon: selectedCategoryIcon.value,
          id: keygen.key
        })
        .then(() => {
          selectedCategoryName.value = null;
          selectedColor.value = null;
          selectedCategoryIcon.value = null;
        });
    }
  };

  const selectIconForCategory = (icon) => {
    console.log("select icon : ", icon)
    selectedCategoryIcon.value = icon
    editingCategory.value.icon = icon
  }

  const removeCategory = (cat) => {
    const catIndex = categoryList.value.indexOf(cat);
    database
      .ref(`${CATEGORY_REF}/${categoryList.value[catIndex].id}`)
      .remove()
      .then(() => {
        getCategoryList()
      })
  }

  database.ref(CATEGORY_REF).on("value", (snapshot) => {
    if (snapshot.val()) {
      categoryList.value = Object.values(snapshot.val());
      console.log("🚀 ~ file: useCategory.js ~ line 49 ~ database.ref ~ categoryList.value", categoryList.value)
    }
  });

  const getCategoryList = () => {
    database.ref(CATEGORY_REF).once("value", (snapshot) => {
      if (snapshot.val()) {
        categoryList.value = Object.values(snapshot.val());
      }
    });
  }

  const editCategory = (category) => {
    editingCategory.value = category
  }

  const validateCategory = async (cat) => {
    database.ref(`${CATEGORY_REF}/${cat.id}`).update(
      {
        name: cat.name, 
        id: cat.id,
        color: cat.color,
        icon: cat.icon
      })
      .then(() => true)
  }

  const getCategoryById = async (id) => {
    return await new Promise(resolve => {
      database.ref(CATEGORY_REF).get()
      .then((snapshot) => {
        if (snapshot.val()) {
          const result = Object.values(snapshot.val())
          const category = result.filter((cat) => cat.id === id)
          if (category.length > 0) {
            resolve(category[0])
          }
          resolve(null)
        }
      })
    })
  }

  return {
    selectedColor,
    selectedCategoryName,
    categoryList,
    editingCategory,
    saveCategory,
    removeCategory,
    getCategoryById,
    editCategory,
    validateCategory,
    getCategoryList,
    selectedCategoryIcon,
    selectIconForCategory
  };
};
