<template>
  <div style="height: 86vh; overflow: auto; width: 50vw; margin: 0 auto;">
    <h1>Graphiques</h1>
    <div v-if="expenseData && expenseData.labels.length > 0">
      <h3>Dépenses</h3>
      <Chart :width="170" type="doughnut" :data="expenseData" />
    </div>
    <br />
    <div v-if="incomeData && incomeData.labels.length > 0">
      <h3>Revenus</h3>
      <Chart :width="170" type="doughnut" :data="incomeData" />
    </div>
  </div>
  <h3 v-if="!expenseData && !incomeData">Pas de données</h3>
  <div
    v-else
    style="color:white; background: black; margin: 0 auto; position:sticky; bottom:0; width: max-content; padding: 10px;"
  >
    Total =
    {{ delta() }} €
  </div>
</template>

<script>
import { defineComponent } from "vue";
import { graph } from "../composables/useGraph";

export default defineComponent({
  name: "Graph",
  setup() {
    const { expenseData, incomeData } = graph();

    const delta = () => {
      const incomeList = incomeData.value.datasets;
      const expenseList = expenseData.value.datasets;
      let incomes = [];
      let expenses = [];
      incomeList.forEach((element) => {
        incomes = element.data.map((value) => parseFloat(value));
      });
      expenseList.forEach((element) => {
        expenses = element.data.map((value) => parseFloat(value));
      });
      return `${(sum(incomes) + sum(expenses)).toFixed(2)}`;
    };

    const sum = (numbers) => {
      if (numbers.length > 0) {
        let result = numbers.reduce((acc = 0, value) => acc + value);
        return result;
      }
      return 0;
    };

    return {
      expenseData,
      incomeData,
      delta,
    };
  },
});
</script>

<style lang="scss" scoped>
h1,
h3 {
  color: white;
}
</style>
