import { ref } from "vue";
import {
  database,
  CATEGORY_REF,
  TRANSACTION_REF,
} from "../services/firebaseDatabase";

export const graph = () => {
  let expenseData = ref();
  let incomeData = ref();
  let category = database.ref(CATEGORY_REF);
  let transactions = database.ref(TRANSACTION_REF);
  let dataSet = [];

  category.once("value", (snap) => {
    const categories = Object.values(snap.val());
    categories.forEach((cat) => {
      dataSet.push({
        name: cat.name,
        color: cat.color,
        id: cat.id,
        amount: 0,
      });
    });

    transactions.once("value", (snapshot) => {
      if (snapshot.val()) {
        let transactionResults = Object.values(snapshot.val());
        dataSet.forEach((data) => {
          transactionResults.forEach((res) => {
            if (res.categoryId === data.id) {
              data.amount += res.amount;
            }
          });
        });

        const expenseFilteredData = dataSet.filter((data) => data.amount < 0);
        const incomeFilteredData = dataSet.filter((data) => data.amount > 0);
        const expenseNames = Object.values(
          expenseFilteredData.map((data) => data.name)
        );
        const expenseAmounts = Object.values(
          expenseFilteredData.map((data) => data.amount)
        );
        const expenseColors = Object.values(
          expenseFilteredData.map((data) => data.color)
        );
        const incomeNames = Object.values(
          incomeFilteredData.map((data) => data.name)
        );
        const incomeAmounts = Object.values(
          incomeFilteredData.map((data) => data.amount)
        );
        const incomeColors = Object.values(
          incomeFilteredData.map((data) => data.color)
        );

        expenseData.value = {
          labels: expenseNames,
          datasets: [
            {
              data: expenseAmounts,
              backgroundColor: expenseColors,
            },
          ],
        };

        incomeData.value = {
          labels: incomeNames,
          datasets: [
            {
              data: incomeAmounts,
              backgroundColor: incomeColors,
            },
          ],
        };
      }
    });
  });

  return {
    expenseData: expenseData,
    incomeData: incomeData,
  };
};
