import { ref } from "vue";
import {
  database,
  CATEGORY_REF,
  TRANSACTION_REF,
} from "../services/firebaseDatabase";
import { useToast } from "primevue/usetoast";
import { category } from "./useCategory"
import { stringHelper } from "../helpers/stringHelper"

export const useTransactions = () => {
  const toast = useToast();
  let editingTransaction = ref();
  let transactionName = ref("");
  let transactionList = ref([]);
  let transactionAmount = ref();
  let selectedCategory = ref();
  let editedCategory = ref();
  let modifiedTransaction = ref({ name: "", amount: 0, id: "" });
  let validate = ref(true);
  const options = {
    weekday: "long",
    year: "numeric",
    month: "long",
    day: "numeric",
    minute: "numeric",
    hour: "numeric",
    second: "numeric",
  };

  const { getCategoryById } = category()
  const { dotToComma, commaToDot } = stringHelper()

  const validateTransaction = (transaction) => {
    modifiedTransaction.value = { name: "", amount: 0, id: "" };
    transaction.categoryId = editedCategory.value.id
    editedCategory.value = null
    const dateIdentifier = new Date().toLocaleDateString("fr-FR", options);
    const transactionRef = database.ref(`${TRANSACTION_REF}/${transaction.id}`);
    transactionRef.set({
      amount: transaction.amount,
      name: transaction.name,
      date: dateIdentifier,
      id: transaction.id,
      categoryId: transaction.categoryId
    });
    getTransactions()
  };

  const cancelTransaction = (transaction) => {
    modifiedTransaction.value = { name: "", amount: 0, id: "" };
    const transactionIndex = transactionList.value.indexOf(transaction);
    transactionList.value[transactionIndex] = editingTransaction.value;
  };

  const editTransaction = async (transaction) => {
    editedCategory.value = await getCategoryById(transaction.categoryId)
    modifiedTransaction.value = transaction;
    editingTransaction.value = {
      amount: transaction.amount,
      id: transaction.id,
      date: transaction.date,
      name: transaction.name,
      color: transaction.color,
      icon: transaction.icon,
      categoryId: transaction.categoryId
    };
  };

  const removeTransaction = (transaction) => {
    const transactionIndex = transactionList.value.indexOf(transaction);
    database
      .ref(`${TRANSACTION_REF}/${transactionList.value[transactionIndex].id}`)
      .remove();
    transactionList.value.splice(transactionIndex, 1);
  };

  const addTransaction = () => {
    if (
      transactionName.value.length > 0 &&
      transactionAmount.value !== 0 &&
      transactionAmount.value
    ) {
      transactionList.value.push({
        name: transactionName.value,
        amount: transactionAmount.value,
        id: `${transactionName.value}-${Math.floor(Math.random() * 1000000)}`,
        categoryId: selectedCategory.value
          ? selectedCategory.value.id
          : transactionAmount.value < 0
            ? "Autres dépenses"
            : "Autres revenus",
      });
      transactionName.value = "";
      transactionAmount.value = null;
      const dateIdentifier = new Date().toLocaleDateString("fr-FR", options);
      const transactionRef = database.ref(
        `${TRANSACTION_REF}/${
          transactionList.value[transactionList.value.length - 1].id
        }`
      );
      transactionRef.set({
        amount: transactionList.value[transactionList.value.length - 1].amount,
        name: transactionList.value[transactionList.value.length - 1].name,
        date: dateIdentifier,
        id: transactionList.value[transactionList.value.length - 1].id,
        categoryId:
          transactionList.value[transactionList.value.length - 1].categoryId,
      });
    } else {
      validate.value = false;
      timeoutAlert();
      toast.add({
        severity: "error",
        summary: "Erreur",
        detail: "Merci de remplir les deux champs",
        life: 3000,
      });
    }
    getTransactions();
  };

  const getTransactions = () => {
    database
      .ref(TRANSACTION_REF)
      .get()
      .then((snapshot) => {
        const data = snapshot.val();
        transactionList.value.length = 0;
        if (data) {
          const dataSet = Object.values(data);
          dataSet.forEach((transaction) => {
            let cat;
            database
              .ref(CATEGORY_REF)
              .once("value")
              .then((snapshot) => {
                if (snapshot.val()) {
                  const catList = Object.values(snapshot.val());
                  let catIndex = catList.findIndex(
                    (c) => c.id === transaction.categoryId
                  );
                  if (catIndex !== -1) {
                    cat = catList[catIndex];
                  }
                }

                if (cat) {
                  transaction.color = cat.color;
                  transaction.icon = cat.icon;
                } else {
                  transaction.color = "mediumseagreen";
                }
                transactionList.value.push(transaction);
              });
          });
        }
      })
      .catch((err) => {
        console.error("Erreur get transactions :", err);
      });
  };

  const timeoutAlert = () => {
    setTimeout(() => (validate.value = true), 2500);
  };

  const changeSign = (tagId, editedValue) => {
    let tag = document.getElementById(tagId);
    let inputString = tag.value;
    if (inputString !== "") {
      let valueString = 
        commaToDot(inputString.slice(0, inputString.length - 2))
        .match(/[0-9,\\.,\\-]/g)
        .reduce((acc, currentValue) => acc + currentValue);
        console.log("valueString = ", valueString)
      let reverseValue = parseFloat(valueString) * -1.0;
      if (Number.isInteger(reverseValue)) {
        tag.value = `${reverseValue},00 €`;
      } else {
        const result = dotToComma(reverseValue);
        tag.value = `${commaToDot(reverseValue)}${
          result.indexOf(",") === result.length - 2 ? "0" : ""
        } €`;
      }
      if (editedValue) {
        editedValue.amount = reverseValue;
      }
      tag.blur();
    }
  };

  return {
    cancelTransaction,
    validateTransaction,
    removeTransaction,
    addTransaction,
    transactionName,
    transactionList,
    transactionAmount,
    modifiedTransaction,
    validate,
    timeoutAlert,
    getTransactions,
    editTransaction,
    changeSign,
    selectedCategory,
    editedCategory
  };
};
