import { ref } from "vue";
import { iconList } from "../helpers/iconsHelper"

export const useMenu = () => {
  let menu = ref();
  let items = ref([]);

  const {
    bookIcon,
    barChartIcon,
    tagsIcon
  } = iconList()

  items = [
    {
      label: "Previsionnel",
      icon: bookIcon,
      to: "/",
    },
    {
      label: "Graph",
      icon: barChartIcon,
      to: "/graph",
    },
    {
      label: "Catégories",
      icon: tagsIcon,
      to: "/category",
    },
  ];

  const toggle = (event) => {
    menu.value.toggle(event);
  };

  return {
    items,
    toggle,
    menu,
  };
};
