<template>
  <h1>Catégories</h1>
  <div style="display: flex; align-items: center; margin: auto; width: 300px;">
    <div class="p-inputgroup">
      <span
        id="colorPickerForChoice"
        class="p-inputgroup-addon"
        style="heigth:60px; width:60px;"
      />
      <span class="p-inputgroup-addon">
        <i
          :class="selectedCategoryIcon ? selectedCategoryIcon : questionCircle"
          @click="showIconDialog()"
        />
      </span>
      <div class="p-float-label">
        <InputText id="categoryLabel" v-model="selectedCategoryName" />
        <label for="categoryLabel">Nom de la catégorie</label>
      </div>
      <span class="p-inputgroup-addon">
        <i :class="plusIcon" @click="saveCategory()" />
      </span>
    </div>
  </div>
  <br />
  <div style="overflow:auto; heigth:70vh;">
    <div
      v-for="cat in categoryList"
      :key="cat.name"
      style="display: flex; justify-content: flex-end; margin: auto; width: 300px; color:white; padding-right:10px; padding-left:10px;"
    >
      <div style="align-self: center; flex:1; text-align:start">
        {{ cat.name }}
      </div>
      <Avatar
        :icon="cat.icon"
        :style="
          `background-color:${cat.color}; width:36px; height:36px; margin-top: 5px; margin-bottom:5px; color:black;`
        "
      />
      <Button
        :disabled="
          cat.name === 'Autres dépenses' || cat.name === 'Autres revenus'
            ? true
            : false
        "
        :icon="trashIcon"
        class="p-button-secondary"
        style="width:36px; height:36px; margin:5px;"
        @click="removeCategory(cat)"
      />
      <Button
        :disabled="
          cat.name === 'Autres dépenses' || cat.name === 'Autres revenus'
            ? true
            : false
        "
        :icon="pencilIcon"
        class="p-button-secondary"
        style="width:36px; height:36px; margin:5px;"
        @click="showEditDialog(cat)"
      />
    </div>
  </div>

  <div>
    <Dialog v-model:visible="displayEditDialog" :style="{ height: '70vh' }">
      <template #header>
        <h3>Modifier la catégorie</h3>
      </template>

      <div style="height: auto;">
        <div
          style="display: flex; align-items: center; margin: auto; width: 300px;"
        >
          <div class="p-inputgroup">
            <span
              id="colorPickerForEdit"
              class="p-inputgroup-addon"
              style="heigth:60px; max-width:60px;"
            />
            <InputText
              v-model="editingCategory.name"
              placeholder="Nom de la catégorie"
            />
            <span class="p-inputgroup-addon">
              <i :class="plusIcon" @click="saveCategory()" />
            </span>
          </div>
        </div>

        <div style="margin-top: .5em;">
          <transition-group
            name="dynamic-box"
            tag="div"
            class="p-grid"
            style="display:flex; width:80vw; flex-wrap: wrap;"
          >
            <div v-for="icon in allIcons" :key="icon" class="p-col">
              <div style="padding:2px;">
                <Avatar
                  :icon="icon"
                  :class="[
                    selectedCategoryIcon === icon
                      ? 'selected-icon'
                      : 'unselected-icon',
                  ]"
                  @click="selectIconForCategory(icon)"
                />
              </div>
            </div>
          </transition-group>
        </div>
      </div>

      <template #footer style="z-index:20">
        <Button
          label="Annuler"
          :icon="timesIcon"
          class="p-button-text"
          @click="cancelEditDialog"
        />
        <Button
          label="Modifier"
          :icon="checkIcon"
          autofocus
          @click="confirmEdit(editingCategory)"
        />
      </template>
    </Dialog>

    <Dialog v-model:visible="displayIconDialog" :style="{ height: '70vh' }">
      <template #header>
        <h3>Choisir l'icone de la catégorie</h3>
      </template>

      <div style="margin-top: .5em;">
        <transition-group
          name="dynamic-box"
          tag="div"
          class="p-grid"
          style="display:flex; width:80vw; flex-wrap: wrap;"
        >
          <div v-for="icon in allIcons" :key="icon" class="p-col">
            <div style="padding:2px;">
              <Avatar
                :icon="icon"
                :class="[
                  selectedCategoryIcon === icon
                    ? 'selected-icon'
                    : 'unselected-icon',
                ]"
                @click="selectIconForCategory(icon)"
              />
            </div>
          </div>
        </transition-group>
      </div>

      <template #footer style="z-index:20">
        <Button
          label="Annuler"
          :icon="timesIcon"
          class="p-button-text"
          @click="cancelIconChoice"
        />
        <Button
          label="Modifier"
          :icon="checkIcon"
          autofocus
          @click="confirmIconChoice(selectIconForCategory)"
        />
      </template>
    </Dialog>
  </div>
</template>

<script>
import { defineComponent, ref, onMounted, watchEffect } from "vue";
import { category } from "../composables/useCategory";
import { iconList } from "../helpers/iconsHelper";
import Picker from "vanilla-picker";

export default defineComponent({
  name: "Category",
  setup() {
    const {
      selectedColor,
      selectedCategoryName,
      categoryList,
      saveCategory,
      removeCategory,
      editCategory,
      editingCategory,
      validateCategory,
      getCategoryList,
      selectedCategoryIcon,
      selectIconForCategory,
    } = category();

    const {
      checkIcon,
      timesIcon,
      plusIcon,
      trashIcon,
      pencilIcon,
      questionCircle,
      allIcons,
    } = iconList();

    let displayEditDialog = ref(false);
    let displayIconDialog = ref(false);

    const confirmEdit = async (cat) => {
      displayEditDialog.value = false;
      await validateCategory(cat);
      selectedCategoryIcon.value = null;
    };

    const confirmIconChoice = (icon) => {
      displayIconDialog.value = false;
      console.log("icon selected for creation : ", icon);
    };

    const cancelIconChoice = () => {
      displayIconDialog.value = false;
      selectedCategoryIcon.value = null;
      console.log("icon choice cancelled");
    };

    const showIconDialog = () => {
      displayIconDialog.value = true;
    };

    const cancelEditDialog = () => {
      displayEditDialog.value = false;
      getCategoryList();
    };

    const showEditDialog = (cat) => {
      displayEditDialog.value = true;
      selectedCategoryIcon.value = cat.icon;
      selectedColor.value = cat.color.substring(1);
      editCategory(cat);
    };

    const stopWatcher = watchEffect(() => {
      console.log("watchEffect : ");

      if (displayEditDialog.value) {
        const interval = setInterval(() => {
          console.log("set picker 2");
          var parentTwo = document.querySelector("#colorPickerForEdit");
          var pickerTwo = new Picker(parentTwo);
          parentTwo.style.background = "#" + selectedColor.value;

          pickerTwo.onDone = function(color) {
            parentTwo.style.background = color.hex;
            selectedColor.value = color.hex.substring(1);
            editingCategory.value.color = color.hex;
            console.log("picker 2 :", color.hex);
          };

          clearInterval(interval);
        }, 500);
      }
    });

    onMounted(() => {
      var parentOne = document.querySelector("#colorPickerForChoice");
      var pickerOne = new Picker(parentOne);

      pickerOne.onDone = function(color) {
        parentOne.style.background = color.hex;
        selectedColor.value = color.hex.substring(1);
        console.log("picker 1 :", color.hex);
      };
    });

    return {
      selectedColor,
      selectedCategoryName,
      categoryList,
      saveCategory,
      removeCategory,
      editCategory,
      displayEditDialog,
      confirmEdit,
      cancelEditDialog,
      showEditDialog,
      editingCategory,
      checkIcon,
      timesIcon,
      plusIcon,
      trashIcon,
      pencilIcon,
      questionCircle,
      iconList,
      selectedCategoryIcon,
      selectIconForCategory,
      allIcons,
      showIconDialog,
      confirmIconChoice,
      cancelIconChoice,
      displayIconDialog,
      stopWatcher,
    };
  },
});
</script>

<style lang="scss" scoped>
h1 {
  color: white;
}

.selected-icon {
  background-color: rgb(0, 208, 104);
}

.unselected-icon {
  background-color: #2196f3;
  color: white;
}
</style>
