<template>
  <h1 style="color: white; margin:0;">
    Prévisionnel
  </h1>
  <TransactionInput style="height:50vh;" :totalCallback="receiveTotal" />
  <div style="position:sticky; bottom:30px; display: flex; flex-direction: column-reverse; align-items: center;" >
    <div class="footer">
      <p class="p-mb-3">TOTAL : {{ total }} €</p>
    </div>
    <Button
      class="p-button-raised"
      label="Sauvegarder"
      :icon="checkIcon"
      iconPos="right"
      @click="savePrevisionnel"
    />
  </div>

</template>

<script>
import TransactionInput from "../components/TransactionInput";
import { onMounted, ref, defineComponent } from "vue";
import { database, PREVISIONNEL_REF } from "../services/firebaseDatabase";
import { iconList } from "../helpers/iconsHelper";

export default defineComponent({
  name: "Previsionnel",
  components: {
    TransactionInput,
  },
  setup() {
    let total = ref(0);
    const receiveTotal = (newTotal) => {
      total.value = newTotal.toFixed(2);
    };

    const { 
      checkIcon,
    } = iconList()

    const previsionnelRef = database.ref(PREVISIONNEL_REF);

    const savePrevisionnel = () => {
      const options = {
        weekday: "long",
        year: "numeric",
        month: "long",
        day: "numeric",
        minute: "numeric",
        hour: "numeric",
        second: "numeric",
      };
      const dateIdentifier = new Date().toLocaleDateString("fr-FR", options);
      const messageRef = database.ref(`${PREVISIONNEL_REF}/${dateIdentifier}`);
      messageRef.set({
        total: total.value,
        currency: "Euro",
      });
    };

    onMounted(() => {
      previsionnelRef.on("value", (snapshot) => {
        console.log(
          "previsionnels sauvegardés : " + JSON.stringify(snapshot, null, 2)
        );
      });
    });

    return {
      receiveTotal,
      total,
      savePrevisionnel,
      checkIcon
    };
  },
});
</script>

<style lang="scss" scoped>
.footer {
  & p {
    position: relative;
    margin: 0 auto;
    width: 300px;
    color: white;
    background-color: rgb(51, 63, 238);
  }
}
</style>
