<template>
  <div
    style="position: sticky; margin-left:7px; top:5px; z-index:10; text-align:start;"
  >
    <Button :icon="burgerIcon" type="button" @click="toggle" />
    <TieredMenu ref="menu" :model="items" :popup="true" />
  </div>
  <router-view style="overflow: auto"></router-view>
  <div id="sign-in" />
</template>

<script>
import firebase from "./services/firebase";
import { useMenu } from "./composables/useMenu";
import { iconList } from "./helpers/iconsHelper";

export default {
  name: "Application",
  setup() {
    firebase
      .auth()
      .signInWithEmailAndPassword("e.perrard@gmail.com", "Yolo13")
      .then((userCredential) => {
        // Signed in
        const user = userCredential.user;
        user;
        console.log("user ...");
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(errorCode, errorMessage);
      });

    const initApp = function() {
      firebase.auth().onAuthStateChanged(
        function(user) {
          if (user) {
            // User is signed in.
            console.log("user is sign in : ...");
            user.getIdToken().then(function(accessToken) {
              accessToken;
              console.log(`accessToken : ...`);
            });
          } else {
            document.getElementById("sign-in").textContent = "Sign in";
          }
        },
        function(error) {
          console.log(error);
        }
      );
    };

    window.addEventListener("load", function() {
      initApp();
    });

    const { items, toggle, menu } = useMenu();
    const { burgerIcon } = iconList();

    return {
      items,
      toggle,
      menu,
      burgerIcon,
    };
  },
};
</script>

